<template>
    <div class="table">
        <div class="handle-box">
            <div class="crumbs">
                <span>快速开户</span>
            </div>
            <el-form ref="form" :inline="true" :rules="rules">

                <el-form-item label="客户类型：" prop="userType">
                    <el-select v-model="userType" @change="khlxFn" placeholder="请选择" class="handle-select">
                        <el-option
                            v-for="item in khlx"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="证件类型：" prop="documentType">
                    <el-select v-model="documentType" placeholder="请选择" class="handle-select">
                        <el-option
                            v-for="item in zjlx"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="证件号码：" prop="documentNo">
                    <el-input placeholder="请输入证件号码" class="handle-input" style="width: 164px;" v-model="documentNo"></el-input>
                    <span class="self_button" @click="jiaoyan">校验</span>
                </el-form-item>
                </br>

                <el-form-item label="客户名称：" prop="userName">
                    <el-input placeholder="请输入客户名称" class="handle-input" v-model="userName"></el-input>
                </el-form-item>

                <el-form-item label="手机号码：" prop="mobile">
                    <el-input placeholder="请输入手机号码" class="handle-input" v-model="mobile"></el-input>
                </el-form-item>

                <el-form-item label="邮箱：">
                    <el-input placeholder="请输入邮箱" class="handle-input" v-model="email"></el-input>
                </el-form-item>

                <el-form-item label="联系地址：" class="input100" prop="address">
                    <el-input placeholder="请输入联系地址" class="input100_1" v-model="address"></el-input>
                </el-form-item>
                </br>

                <el-form-item label="安装地址：" class="input100">
                    <el-input placeholder="请输入安装地址" class="input100_1" v-model="fixAddress"></el-input>
                </el-form-item>
                </br>

                <div v-show="userType == 2 ? true : false">

                    <el-form-item label="分支机构名称：" class="input100" prop="branchName">
                        <el-input placeholder="请输入分支机构名称" class="input100_1" v-model="branchName"></el-input>
                    </el-form-item>
                    </br>

                    <el-form-item label="经办人名称：" prop="agentName">
                        <el-input placeholder="请输入经办人名称" class="handle-input" v-model="agentName"></el-input>
                    </el-form-item>

                    <el-form-item label="经办人证件：" prop="agentDocumentType">
                        <el-select v-model="agentDocumentType" placeholder="请选择" class="handle-select">
                            <el-option
                                v-for="item in jbrzj"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="证件号码：" prop="agentDocumentNo">
                        <el-input placeholder="请输入证件号码" class="handle-input" v-model="agentDocumentNo"></el-input>
                    </el-form-item>

                </div>

                <el-form-item label="证件照：" style="width: 100%" prop="documentPhotos">

                    <ul id="photos">
                        <li v-for="(item, index) in documentPhotos" :key="index">
                            <img :src="item.path" width="160" height="auto">
                            <i class="el-icon-error self_delete" @click="self_delete(index)"></i>
                        </li>
                    </ul>

                    <el-button type="button" @click="self_upload" class="el-button el-button--primary el-button--small">点击上传</el-button>

                    <div style="display: none;">
                        <form id="uploadform" target="uploadform" enctype="multipart/form-data" method="post">
                            <input id="uploadfile" type="file" name="file" accept="image/png, image/jpeg, image/gif, image/jpg">
                            <input id="uploadsubmit" type="submit" value="上传" >
                            <iframe id="uploadiframe" name="uploadform"></iframe>
                        </form>
                    </div>
                        
                </el-form-item>
                </br>

                <el-button type="primary" @click="save" style="float: right;margin-right: 30px;">保存</el-button>
                <el-button type="info" style="float: right;margin-right: 10px;" @click="clear">清空</el-button>
                <!-- <el-button type="success" icon="plus">新增</el-button> -->
                <!-- <el-button type="info">校验</el-button> -->
            </el-form>
        </div>
        
    </div>
</template>

<script>
    export default {
        data() {
            return {
                khlx:[
                    {'name':'个人用户','id':'1'},
                    {'name':'集团用户','id':'2'}
                ],
                zjlx:[],
                userType: '',
                documentType: '',
                documentNo: '',
                userName: '',
                mobile: '',
                email: '',
                address:'',
                fixAddress: '',
                branchName:'',
                agentName:'',
                agentDocumentType:'',
                agentDocumentNo:'',
                documentPhotos:[],
                jbrzj:[
                    {'name':'身份证','id':'101'},
                    {'name':'护照','id':'102'},
                    {'name':'港澳居民来往内地通行证','id':'103'},
                    {'name':'台湾居民来往大陆通行证','id':'104'},
                    {'name':'军官证','id':'105'},
                    {'name':'武警警察身份证','id':'106'}
                ],
                rules:{
                    userType:[{required: true, message: '请输入客户类型', validator: this.self_check}],
                    documentType:[{required: true, message: '请输入证件类型', validator: this.self_check}],
                    documentNo:[{required: true, message: '请输入证件号码', trigger: 'blur', validator: this.self_check}],
                    userName:[{required: true, message: '请输入客户名称', trigger: 'blur', validator: this.self_check}],
                    mobile:[{required: true, message: '请输入手机号码', trigger: 'blur', validator: this.self_check}],
                    address:[{required: true, message: '请输入联系地址', trigger: 'blur', validator: this.self_check}],
                    branchName:[{required: true, message: '请输入分支机构名称', trigger: 'blur', validator: this.self_check}],
                    agentName:[{required: true, message: '请输入经办人名称', trigger: 'blur', validator: this.self_check}],
                    agentDocumentType:[{required: true, message: '', validator: this.self_check}],
                    agentDocumentNo:[{required: true, message: '请输入证件号码', trigger: 'blur', validator: this.self_check}],
                    documentPhotos:[{required: true, message: '', validator: this.self_check}]
                }
            }
        },
        mounted:function() {
            this.judgeUser();
        },
        methods:{
            self_check(rule, value, callback){//自定义验证方法,可自行删改
                // console.log(rule)
                if (!this[rule.field]) {
                    callback(new Error(rule.message))
                }
            },
            judgeIsIE(){
                if(!!window.ActiveXObject || "ActiveXObject" in window){
                    return true;
                }else{
                    return false;
                }
            },
            khlxFn(){
                if(this.userType == 1){
                    this.zjlx = [
                    {'name':'身份证','id':'101'},
                    {'name':'护照','id':'102'},
                    {'name':'港澳居民来往内地通行证','id':'103'},
                    {'name':'台湾居民来往大陆通行证','id':'104'},
                    {'name':'军官证','id':'105'},
                    {'name':'武警警察身份证','id':'106'}
                    ]
                } else if(this.userType == 2){
                    this.zjlx = [
                    {'name':'统一社会信用代码证书','id':'201'},
                    {'name':'组织机构代码证','id':'202'},
                    {'name':'营业执照','id':'203'},
                    {'name':'事业单位法人证书','id':'204'},
                    {'name':'社会团体法人登记证书','id':'205'},
                    {'name':'律师事务所执业许可证','id':'206'}
                    ]
                }
                this.documentType = '';
            },
            jiaoyan(){
                let url = this.baseUrl + "issuing-card/rfid/customer/check?documentNo=" + this.documentNo + "&documentType=" + this.documentType;
                this.$axios.get(url).then(response => {
                    this.$message({
                        message:response.data.msg,
                        center: true
                    })
                })
            },
            self_upload(){
                if (this.documentPhotos.length > 2) {
                    this.$message({
                        message:'最多上传3张证件照！',
                        center:true
                    })
                    return false;
                }
                var self = this;
                document.getElementById('uploadform').setAttribute("action", self.baseUrl + 'issuing-card/core/annex/save');
                document.getElementById('uploadfile').click();
                //判断浏览器兼容性
                if(this.judgeIsIE()){
                    // document.getElementById('uploadfile').onchange = function(){
                        self.$loading();
                        document.getElementById('uploadsubmit').click();
                        document.getElementById('uploadiframe').onload = function(){
                            self.$loading().close();
                            document.getElementById('uploadfile').value = '';
                            try{
                                // var el = (document.getElementById('uploadiframe').contentWindow || document.getElementById('uploadiframe').contentDocument.parentWindow).document.body;
                                var el = (document.getElementById('uploadiframe').contentWindow || document.getElementById('uploadiframe').contentDocument.parentWindow).document.body.innerHTML;
                                let html = el;
                                let json = html ? eval("(" + html + ")") : false;
                                if (json) {
                                    self.$message({
                                        message:json.msg,
                                        center: true
                                    })
                                    if (json.code == 0) {
                                        self.documentPhotos.push(json.data)
                                    }
                                } else {
                                    self.$message({
                                        message:'上传失败！',
                                        center: true
                                    })
                                }
                            }catch(e){
                                self.$message({
                                    message:'上传失败！',
                                    center: true
                                })
                            }
                        }
                    // }
                }else{
                    document.getElementById('uploadfile').onchange = function(){
                        self.$loading();
                        document.getElementById('uploadsubmit').click();
                        document.getElementById('uploadiframe').onload = function(){
                            self.$loading().close();
                            document.getElementById('uploadfile').value = '';
                            try{
                                var el = (document.getElementById('uploadiframe').contentWindow || document.getElementById('uploadiframe').contentDocument.parentWindow).document.body;
                                while(el &&  el.nodeType !== 3){
                                    el = el.firstChild;
                                }
                                let html = el.nodeValue;
                                let json = html ? eval("(" + html + ")") : false;
                                if (json) {
                                    self.$message({
                                        message:json.msg,
                                        center: true
                                    })
                                    if (json.code == 0) {
                                        self.documentPhotos.push(json.data)
                                    }
                                } else {
                                    self.$message({
                                        message:'上传失败！',
                                        center: true
                                    })
                                }
                            }catch(e){
                                self.$message({
                                    message:'上传失败！',
                                    center: true
                                })
                            }
                        }
                    }
                }
            },
            save(){
                let self = this;
                let url = this.baseUrl + 'issuing-card/rfid/customer/create';
                console.log(url);
                let paramdata = {
                    address:this.address,
                    documentNo:this.documentNo,
                    documentType:this.documentType,
                    email:this.email,
                    fixAddress:this.fixAddress,
                    mobile:this.mobile,
                    userName:this.userName,
                    userType:this.userType
                }
                paramdata.documentPhotos = [];
                for (var i = 0; i < self.documentPhotos.length; i++) {
                    paramdata.documentPhotos.push(self.documentPhotos[i].id)
                }
                if (this.userType == 2) {
                    paramdata.agentDocumentNo = this.agentDocumentNo;
                    paramdata.agentDocumentType = this.agentDocumentType;
                    paramdata.agentName = this.agentName;
                    paramdata.branchName = this.branchName;
                }
                this.$axios.post(url,paramdata).then(response => {
                    console.log(response);
                    this.$message({
                        message:response.data.msg,
                        center: true
                    })
                    if(response.data.success){
                        this.$router.push('/yhgl');
                    }
                })
            },
            self_delete(index){
                this.documentPhotos.splice(index,1)
            },
            clear(){
                this.userType =  '',//客户类型
                this.documentType =  '',//证件类型
                this.documentNo =  '',
                this.userName =  '',
                this.mobile =  '',
                this.email =  '',
                this.address = '',
                this.fixAddress =  '',
                this.branchName = '',
                this.agentName = '',
                this.agentDocumentType = '',
                this.agentDocumentNo = '',
                this.documentPhotos = []
            }
        }
    }
</script>

<style scoped>
    .el-form-item{
        width: calc( 33.3% - 20px);
    }
    .input100{
        width: 100%;
    }
    .input100 .input100_1{
        width: 600px;
    }
    .item {
        margin-top: 18px;
        margin-bottom: 18px;
    }
    .cnt {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        position: absolute;
        margin-top: 5px;
        margin-left: 38px;
        background: #f56c6c;
        color: #fff;
        text-align: center;
        line-height: 20px;
    }
    .handle-box {
        margin-bottom: 20px;
    }
    .handle-box .crumbs{
        /*margin: 0 20px 10px;*/
        padding: 15px 20px;
        border: 1px solid #d7dae1;
        color: #003c78;
        background: #fff;
        margin: 0 0 10px;
    }
    .bianji {
        margin-left:0px !important;
    }
    .handle-select {
        /*width: 120px;*/
    }
    .handle-input {
        width: 196px;
        display: inline-block;
    }
    .export_btn{
        color: #ffffff;
    }
    .table{
        text-align: left;
    }
    .self_button{
        font-size: 12px;
        border-radius: 3px;
        color: #fff;
        background-color: #409EFF;
        border-color: #409EFF;
        display: inline-block;
        line-height: 1;
        white-space: nowrap;
        cursor: pointer;
        border: 1px solid #fff;
        text-align: center;
        box-sizing: border-box;
        outline: 0;
        margin: 0;
        transition: .1s;
        font-weight: 500;
        padding: 4px 2px;
    }
    #photos li{
        display: inline-block;
        position: relative;
    }
    .self_delete{
        position: absolute;
        right: 0;
        top: 0;
        background: #fff;
        cursor: pointer;
        border-radius: 3px;
    }
</style>
<style type="text/css">
    .table .handle-box .el-form-item__label{
        width: 120px;
        padding-right: 0px;
    }
</style>



